* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Raleway;
  font-size: 16px;
  font-variant-numeric: lining-nums;
}

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Regular.ttf") format("truetype");
  font-style: normal;
  font-weight: 400;
}

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Medium.ttf") format("truetype");
  font-style: normal;
  font-weight: 500;
}

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-SemiBold.ttf") format("truetype");
  font-style: normal;
  font-weight: 600;
}

@font-face {
  font-family: "Raleway";
  src: url("../fonts/Raleway-Bold.ttf") format("truetype");
  font-style: normal;
  font-weight: 700;
}

html {
  height: 100%;
  scroll-behavior: smooth;
  background-color: #F8F8F8;
}

a {
  color: black;
  text-decoration: none;
}

label {
  display: flex;
  flex-direction: column;
  gap: var(--gap-small);
}

label input {
  width: 100%;
}

input[type=time] {
  width: 100%;
}

input {
  width: 100%;
  padding: var(--padding-small);
  border: var(--border-solid-accent-second);
  border-radius: var(--border-radius-small);
  outline: none;
  height: 40px;
}

button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  height: 40px;
  width: 100%;
  border-radius: var(--border-radius-small);
  border: 2px solid var(--main-color);
  background: transparent;
  padding: 0 var(--padding-big);
  cursor: pointer;
  font-size: 24px;
  font-weight: 500;
}

button svg {
  height: 20px !important;
  aspect-ratio: 1 / 1;
}

.button-default {
  background-color: var(--green-color);
  color: white;
  border: none;
}

.button-square {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  /* padding: 5px; */
  border-radius: var(--border-radius-small);
  border: var(--border-solid-accent-second);
  background: transparent;
  width: auto;
  aspect-ratio: 1 / 1;
}

.button-red {
  background-color: var(--red-color);
}

.hidden {
  display: none !important;

}
h1 {
  /* color: white; */
  text-align: center;
  font-size: 36px;
}

h2 {
  /* color: white; */
  font-size: 24px;
  font-weight: 500;
}

h3 {
  font-size: 20px;
  font-weight: 500;
}

@media (max-width: 570px) {
  h1 {
    font-size: 28px;
  }
  h2 {
    font-size: 24px;
  }
  h3{
    font-size: 18px;
  }
}

.container {
  display: flex;
  flex-direction: column;
  gap: var(--gap-big);
  max-width: 1000px;
  margin: 0 auto;
  padding: 30px var(--padding-small) var(--padding-small) var(--padding-small) ;
}

.component {
  background-color: var(--component-color);
  box-shadow: 0 4px 10px 0px rgba(0, 0, 0, 0.10);
  padding: var(--padding-big);
  border-radius: var(--border-radius-big);
}